.myButton{
    background-color: rgb(183, 183, 255);
    padding: 10px 20px;
    border: none;
    border-radius: 10px;
    margin: 5px;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
}

.myButton:hover {
    background-color: rgb(223, 223, 252);
}

.myButton:active {
    background-color: rgb(29, 29 ,94);
    color: white;
    /* box-shadow: inset 3px 3px 6px rgb(8, 8, 85),
    inset -3px -3px -6px rgb(3, 3, 21); */
}